<template>
  <div class="banner-setting-dialog-table" v-loading="loading">
    <el-table
      class="table-base"
      size="small"
      :data="tableData"
      stripe
      border
      height="350px"
      style="width: 100%"
      row-key="id"
      @expand-change="expandChange"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="expand" width="26">
        <template slot-scope="scope">
          <TableExpand :row="scope.row"></TableExpand>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="52">
        <template slot-scope="scope">
          <el-button size="mini" type="text" @click="handleConfirm(scope.row)">
            确定
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        label="配件编码"
        prop="code"
        width="114"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label="配件名称"
        prop="name"
        min-width="146"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label="性质"
        prop="property"
        min-width="68"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label="产地"
        prop="productionPlace"
        min-width="68"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label="配件品牌"
        prop="brand"
        min-width="88"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label="配件类别"
        prop="partType"
        min-width="88"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label="配件属性"
        prop="partProperty"
        min-width="180"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label="可订货数量"
        prop="orderQty"
        width="92"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label="零售价"
        prop="priceRetail"
        min-width="78"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label="图片"
        prop="image"
        width="57"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          <img class="table-column-img" :src="scope.row.image" />
        </template>
      </el-table-column>
      <el-table-column
        label="单位"
        prop="unitName"
        width="50"
        show-overflow-tooltip
      ></el-table-column>
    </el-table>
    <el-pagination
      class="pagination-base"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      background
      :current-page="pageData.page"
      :page-sizes="pageData.pageSizes"
      :page-size="pageData.pageSize"
      :pager-count="pageData.pagerCount"
      layout="total, sizes, prev, pager, next, jumper"
      :total="pageData.total"
    >
    </el-pagination>
  </div>
</template>

<script>
import { StoreManageService } from "../../services";
const Service = new StoreManageService();
import TableExpand from "../tableExpand.vue";

export default {
  name: "BannerSettingDialogTable",
  props: {
    formData: Object
  },
  components: {
    TableExpand
  },
  data() {
    return {
      pageData: {
        page: 1,
        pageSizes: [10, 20, 50, 100],
        pageSize: 10,
        total: 0,
        pagerCount: 5
      },
      tableData: [
        // {
        //   id: "12987122",
        //   name: "好滋好味鸡蛋仔",
        //   partType: "江浙小吃、小吃零食",
        //   desc: "荷兰优质淡奶，奶香浓而不腻",
        //   address: "上海市普陀区真北路",
        //   shop: "王小虎夫妻店",
        //   shopId: "10333"
        // },
        // {
        //   id: "12987123",
        //   name: "好滋好味鸡蛋仔",
        //   partType: "江浙小吃、小吃零食",
        //   desc: "荷兰优质淡奶，奶香浓而不腻",
        //   address: "上海市普陀区真北路",
        //   shop: "王小虎夫妻店",
        //   shopId: "10333"
        // },
        // {
        //   id: "12987125",
        //   name: "好滋好味鸡蛋仔",
        //   partType: "江浙小吃、小吃零食",
        //   desc: "荷兰优质淡奶，奶香浓而不腻",
        //   address: "上海市普陀区真北路",
        //   shop: "王小虎夫妻店",
        //   shopId: "10333"
        // },
        // {
        //   id: "12987126",
        //   name: "好滋好味鸡蛋仔",
        //   partType: "江浙小吃、小吃零食",
        //   desc: "荷兰优质淡奶，奶香浓而不腻",
        //   address: "上海市普陀区真北路",
        //   shop: "王小虎夫妻店",
        //   shopId: "10333"
        // }
      ],
      selections: []
    };
  },
  methods: {
    // 确定
    handleConfirm(data) {
      this.$emit("handleConfirm", data);
    },
    // 查询
    handleQuery() {
      this.pageData.page = 1;
      this.getGoods();
    },
    getGoods() {
      this.loading = true;
      const params = {
        ...this.formData,
        include: false, // 查询包含商品，true:查询指定的商品;false:查询排除指定的商品
        ids: [], // 商品ID数组
        page: this.pageData.page,
        pageSize: this.pageData.pageSize
      };
      Service.getGoods(params)
        .then(res => {
          this.pickList(res.rows || []);
          this.pageData.total = res.totalSize || 0;
          this.loading = false;
        })
        .catch(() => {
          this.tableData = [];
          this.pageData.page = 1;
          this.pageData.total = 0;
          this.loading = false;
        });
    },
    pickList(list) {
      this.tableData = list.map(item => {
        return {
          ...item,
          loading: false,
          info: {}
        };
      });
    },
    // 复选框事件
    handleSelectionChange(val) {
      this.selections = val;
    },
    handleSizeChange(val) {
      this.pageData.page = 1;
      this.pageData.pageSize = val;
      this.getGoods();
    },
    handleCurrentChange(val) {
      this.pageData.page = val;
      this.getGoods();
    },
    // 获取子层表格数据
    expandChange(row, expandedRows) {
      if (!row.info.id) {
        this.$set(row, "loading", true);
        Service.getGoodsInfo(row.id)
          .then(res => {
            this.$set(row, "loading", false);
            if (res) {
              this.$set(row, "info", res);
            }
          })
          .catch(() => {
            this.$set(row, "loading", false);
          });
      }
    }
  },
  created() {
    this.getGoods();
  }
};
</script>

