<template>
  <el-form
    class="form-label-top"
    size="small"
    label-position="top"
    :model="data"
  >
    <el-form-item>
      <el-checkbox v-model="data.h5">在手机端展示此组件</el-checkbox>
    </el-form-item>
    <el-form-item>
      <span class="input-lable-row">目录类型：</span>
      <el-radio-group v-model="data.setting">
        <el-radio
          v-for="(item, index) in settingOptions"
          :key="index"
          :label="item.value"
          >{{ item.name }}</el-radio
        >
      </el-radio-group>
    </el-form-item>
    <div v-show="data.setting === 2">
      <el-form-item>
        <el-button
          size="small"
          :disabled="data.areas.length >= 20"
          @click="addArea"
        >
          <i class="icon-Plus"></i>
          新增一级目录
        </el-button>
        <div class="input-explain">
          最多可支持创建20个一级目录，每个一级目录下最多可支持创建50个二级目录；建议二级目录图片尺寸：120px*120px或其倍数。
        </div>
      </el-form-item>

      <el-collapse v-model="activeName" accordion>
        <el-collapse-item
          v-for="(item, index) in data.areas"
          :key="index"
          :name="String(index + 1)"
        >
          <div slot="title" class="collapse-item-title">
            <div>一级目录名称：</div>
            <div
              class="input-wrap"
              @click.stop="
                event => {
                  event.stopPropagation();
                }
              "
            >
              <el-input
                v-model.trim="item.name"
                size="small"
                maxlength="8"
              ></el-input>
            </div>
            <div
              class="button"
              @click.stop="sortArea(index, index - 1)"
              v-show="index !== 0"
            >
              上移
            </div>
            <div
              class="button"
              @click.stop="sortArea(index, index + 1)"
              v-show="index < data.areas.length - 1"
            >
              下移
            </div>
            <i class="icon-delete" @click.stop="deleteArea(index)"></i>
          </div>
          <div class="two-list">
            <div
              class="two"
              v-for="(twoItem, twoIndex) in item.details"
              :key="twoIndex"
            >
              <el-form-item class="two-form-item" label="二级目录名称：">
                <i
                  class="icon-delete"
                  @click.stop="deleteAreaTwo(item.details, twoIndex)"
                ></i>
                <el-input
                  v-model.trim="twoItem.name"
                  clearable
                  maxlength="15"
                ></el-input>
                <el-button
                  type="primary"
                  size="small"
                  plain
                  @click="showProSettingDialog(twoItem)"
                >
                  关联商品({{ twoItem.partIds.length }})
                </el-button>
              </el-form-item>
              <el-form-item label="上传图片：">
                <ImgSingleUpload
                  :fileUrl.sync="twoItem.picture"
                  :max-size="2"
                />
              </el-form-item>
            </div>
          </div>
          <div class="two-button-wrap">
            <el-button
              size="small"
              :disabled="data.areas.length >= 50"
              @click="addAreaTwo(item)"
            >
              <i class="icon-Plus"></i>
              新增二级目录
            </el-button>
          </div>
        </el-collapse-item>
      </el-collapse>
    </div>

    <ProSettingDialog
      :now="nowData"
      @hide="hideProSettingDialog"
      v-if="proSettingDialogVisible"
    ></ProSettingDialog>
  </el-form>
</template>

<script>
import ImgSingleUpload from "@/components/Upload/ImgSingleUpload.vue";
import ProSettingDialog from "../proSettingDialog.vue";
import { ProListSettingImg } from "@/constants/imagePath.js";

export default {
  name: "ProListSetting",
  props: {
    data: Object
  },
  components: {
    ImgSingleUpload,
    ProSettingDialog
  },
  data() {
    return {
      proSettingDialogVisible: false,
      nowData: {},
      activeName: "1",
      settingOptions: [
        {
          name: "按配件品牌",
          value: 0
        },
        {
          name: "配件类别",
          value: 1
        },
        {
          name: "自定义",
          value: 2
        }
      ]
    };
  },
  methods: {
    // 新增一级目录
    addArea() {
      this.data.areas.push({
        name: "",
        details: [
          {
            picture: ProListSettingImg,
            name: "",
            partIds: []
          }
        ]
      });
    },
    // 新增二级目录
    addAreaTwo(item) {
      item.details.push({
        picture: ProListSettingImg,
        name: "",
        partIds: []
      });
    },
    deleteArea(index) {
      this.data.areas.splice(index, 1);
    },
    // 删除二级目录
    deleteAreaTwo(arr, index) {
      // this.data.areas[index].splice(twoIndex, 1);
      arr.splice(index, 1);
    },
    sortArea(index, toIndex) {
      this.data.areas[index] = this.data.areas.splice(
        toIndex,
        1,
        this.data.areas[index]
      )[0];
    },
    getFileList(files, twoItem) {
      twoItem.pictures = files;
      this.$forceUpdate();
    },
    // 关联商品
    showProSettingDialog(data) {
      this.nowData = data;
      this.proSettingDialogVisible = true;
    },
    hideProSettingDialog() {
      this.nowData = {};
      this.proSettingDialogVisible = false;
    }
  }
};
</script>

