<template>
  <div class="pro-list-dialog-table" v-loading="loading">
    <div class="card-title">{{ isInclude ? "已关联的商品" : "全部商品" }}</div>
    <el-table
      ref="table"
      class="table-base"
      size="small"
      :data="tableData"
      stripe
      border
      height="250px"
      style="width: 100%"
      row-key="id"
      @expand-change="expandChange"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="expand" width="26">
        <template slot-scope="props">
          <TableExpand :row="props.row"></TableExpand>
        </template>
      </el-table-column>
      <el-table-column
        type="selection"
        header-align="center"
        width="43"
      ></el-table-column>
      <el-table-column label="操作" width="52">
        <template slot-scope="scope">
          <el-button
            v-if="isInclude"
            class="color-error"
            size="mini"
            type="text"
            @click="handleRemove(scope.row)"
          >
            移除
          </el-button>
          <el-button
            v-else
            size="mini"
            type="text"
            @click="handleRevence(scope.row)"
          >
            关联
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        label="配件编码"
        prop="code"
        width="114"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label="配件名称"
        prop="name"
        min-width="146"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label="性质"
        prop="property"
        min-width="68"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label="产地"
        prop="productionPlace"
        min-width="68"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label="配件品牌"
        prop="brand"
        min-width="88"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label="配件类别"
        prop="partType"
        min-width="88"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label="配件属性"
        prop="partProperty"
        min-width="180"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label="可订货数量"
        prop="orderQty"
        width="92"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label="零售价"
        prop="priceRetail"
        min-width="78"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label="图片"
        prop="image"
        width="57"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          <!-- <img class="table-column-img" :src="scope.row.image" /> -->
          <el-image
            :z-index="3000"
            style="display: table-cell; width: 36px; height: 36px;"
            :src="scope.row.image"
            :preview-src-list="[scope.row.image]"
          >
            <img slot="error" class="not-available-img" :src="NotAvailableImg" />
          </el-image>
        </template>
      </el-table-column>
      <el-table-column
        label="单位"
        prop="unitName"
        width="50"
        show-overflow-tooltip
      ></el-table-column>
    </el-table>
    <div class="table-bottom">
      <div class="batch-operation">
        <el-checkbox v-model="selectionAll">
          {{ isInclude ? "全选" : "全选当前查询结果" }}</el-checkbox
        >
        <el-divider direction="vertical" class="divider-base"></el-divider>
        <span>
          已选中
          {{ selectionAll ? pageData.total : selections.length }}
          条
        </span>
        <el-divider direction="vertical" class="divider-base"></el-divider>
        <span>批量操作</span>
        <el-button
          v-if="!isInclude"
          size="mini"
          type="primary"
          :disabled="!(selectionAll || selections.length)"
          @click="batchRelevance"
        >
          关联商品
        </el-button>
        <el-button
          v-if="isInclude"
          size="mini"
          type="danger"
          plain
          :disabled="!(selectionAll || selections.length)"
          @click="batchRemove"
        >
          移除商品
        </el-button>
      </div>
      <el-pagination
        class="pagination-base"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        background
        :current-page="pageData.page"
        :page-sizes="pageData.pageSizes"
        :page-size="pageData.pageSize"
        :pager-count="pageData.pagerCount"
        layout="total, sizes, prev, pager, next, jumper"
        :total="pageData.total"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { StoreManageService } from "../services";
const Service = new StoreManageService();
import TableExpand from "./tableExpand.vue";
import NotAvailableImg from "@/assets/img/store/not-available.png";

export default {
  name: "GoodsTable",
  props: {
    type: String,
    formData: Object
  },
  components: {
    TableExpand
  },
  computed: {
    isInclude() {
      return this.type === "include";
    }
  },
  data() {
    return {
      NotAvailableImg,
      selections: [],
      selectionAll: false,
      pageData: {
        page: 1,
        pageSizes: [10, 20, 50, 100],
        pageSize: 10,
        total: 0,
        pagerCount: 5
      },
      loading: false,
      tableData: [
        // {
        //   id: "12987122",
        //   name: "好滋好味鸡蛋仔",
        //   partType: "江浙小吃、小吃零食",
        //   desc: "荷兰优质淡奶，奶香浓而不腻",
        //   address: "上海市普陀区真北路",
        //   shop: "王小虎夫妻店",
        //   shopId: "10333"
        // },
        // {
        //   id: "12987123",
        //   name: "好滋好味鸡蛋仔",
        //   partType: "江浙小吃、小吃零食",
        //   desc: "荷兰优质淡奶，奶香浓而不腻",
        //   address: "上海市普陀区真北路",
        //   shop: "王小虎夫妻店",
        //   shopId: "10333"
        // },
        // {
        //   id: "12987125",
        //   name: "好滋好味鸡蛋仔",
        //   partType: "江浙小吃、小吃零食",
        //   desc: "荷兰优质淡奶，奶香浓而不腻",
        //   address: "上海市普陀区真北路",
        //   shop: "王小虎夫妻店",
        //   shopId: "10333"
        // },
        // {
        //   id: "12987126",
        //   name: "好滋好味鸡蛋仔",
        //   partType: "江浙小吃、小吃零食",
        //   desc: "荷兰优质淡奶，奶香浓而不腻",
        //   address: "上海市普陀区真北路",
        //   shop: "王小虎夫妻店",
        //   shopId: "10333"
        // }
      ]
    };
  },
  methods: {
    // 关联
    handleRevence(data) {
      this.formData.ids.push(data.id);
      this.$nextTick(() => {
        this.$emit("handleQueryAll");
      });
    },
    // 移除
    handleRemove(data) {
      this.formData.ids.splice(
        this.formData.ids.findIndex(id => id === data.id),
        1
      );
      this.$nextTick(() => {
        this.$emit("handleQueryAll");
      });
    },
    // 批量关联
    batchRelevance() {
      if (this.selectionAll) {
        this.getGoods();
      } else {
        this.formData.ids = Array.from(
          new Set([
            ...this.formData.ids,
            ...this.selections.map(item => item.id)
          ])
        );
        this.$emit("submit");
      }
    },
    // 批量移除
    batchRemove() {
      if (this.selectionAll) {
        this.formData.ids = [];
        this.selectionAll = false;
      } else {
        const selectionIds = this.selections.map(item => item.id);
        const ids = [];
        this.formData.ids.forEach(id => {
          if (!selectionIds.includes(id)) {
            ids.push(id);
          }
        });
        this.formData.ids = ids;
      }
      this.$emit("submit");
    },
    // 查询
    handleQuery() {
      this.pageData.page = 1;
      this.getGoods();
    },
    getGoods() {
      this.loading = true;
      // 清除展开状态
      this.toggleRowExpansion();
      let params = {};
      if (this.isInclude) {
        // 查询指定的商品
        // params.include = true; // 查询包含商品，true:查询指定的商品;false:查询排除指定的商品
        params = {
          include: true, // 查询包含商品，true:查询指定的商品;false:查询排除指定的商品
          ids: this.formData.ids // 商品ID数组
        };
      } else {
        // 查询排除指定的商品
        params = {
          ...this.formData
        };
        // 全选当前查询结果
        if (this.selectionAll) {
          params.respType = 1; // 只返回商品ID
        } else {
          params.respType = 0; // 商品所有信息
        }
      }
      params.page = this.pageData.page;
      params.pageSize = this.pageData.pageSize;
      Service.getGoods(params)
        .then(res => {
          if (this.selectionAll) {
            this.selectionAll = false;
            this.formData.ids = res || [];
            this.$emit("submit");
          } else {
            this.pickList(res.rows || []);
            this.pageData.total = res.totalSize || 0;
          }
          this.loading = false;
        })
        .catch(() => {
          this.pageData.page = 1;
          this.pageData.total = 0;
          this.tableData = [];
          this.loading = false;
        });
    },
    pickList(list) {
      this.tableData = list.map(item => {
        return {
          ...item,
          loading: false,
          info: {}
        };
      });
    },
    // 复选框事件
    handleSelectionChange(val) {
      this.selections = val;
    },
    handleSizeChange(val) {
      this.pageData.page = 1;
      this.pageData.pageSize = val;
      this.getGoods();
    },
    handleCurrentChange(val) {
      this.pageData.page = val;
      this.getGoods();
    },
    // 获取子层表格数据
    expandChange(row) {
      if (!row.info.id) {
        this.$set(row, "loading", true);
        Service.getGoodsInfo(row.id)
          .then(res => {
            this.$set(row, "loading", false);
            if (res) {
              this.$set(row, "info", res);
            }
          })
          .catch(() => {
            this.$set(row, "loading", false);
          });
      }
    },
    toggleRowExpansion() {
      const tableEl = this.$refs.table;
      if (tableEl) {
        this.tableData.forEach(item => {
          tableEl.toggleRowExpansion(item, false);
        });
      }
    }
  },
  created() {
    this.getGoods();
  }
};
</script>

